@import '@doggo-memory-game/styles/mixins/colors';

.home-page {
    min-height: 500px;
    width: 100%;
    margin: 0 auto;

    
    .intro-section {
        text-align: center;

        h1 {
            font-size: 50px;
            margin-top: 0;
            margin-bottom: 0;

            color: get-color(secondary);
        }
        h2 {
            color: get-color(tertiary);
        }
        custom-button {
            margin: 10px;
        }

    }
    .cards-section {
        min-height: 500px;
    }
    .complete-modal {
        text-align: center;

        h2 {
            color: get-color(secondary);
        }
        custom-button {
            margin: 10px;
        }
        .image-wrapper {
            height:70%;
            img {
                height:100%; 
            }
        }
    }
    .timer {
        position: fixed;
        right: 15px;
    }
}